h1 
  color: red
  font-size: 20px

@mixin button-bg($bg) 
  background: $bg
  &:hover 
    background: darken($bg,8%)
    transition: all 0.3s ease
  
  &:active 
    background: darken($bg,25%)
   
.btn 
 color: white
 text-decoration: none
 padding:5px 10px
 border-radius:3px
 font-family: 'Poiret One', cursive
 font-size:2em


.btn-green 
   @include button-bg(#2ecc71)